import Header from "./component/header/Header";
import "./styles.scss";
import FootNav from "./component/FootNav";
import {Container} from "@material-ui/core";
import {Route, Routes} from "react-router-dom";
import CurrentGames from "./pages/CurrentGames/CurrentGames";
import History from "./pages/History/History";
import Settings from "./pages/Settings/Settings";
import {useEffect, useState} from "react";
import i18n from "./i18n"; //  i18n
import axios from "axios";
import BackendConfig from "./config/config";
import MakeModel from "./pages/MakeModel/MakeModel";
import AdminPanel from "./pages/AdminPanel/AdminPanel";


export default function App() {

    const [tgUser, setTgUser] = useState(null);
    const [location, setLocation] = useState({
        ip: "...",
        country: "",
        city: "",
        lat: 0.0,
        lon: 0.0,
    });
    const [userData, setUserData] = useState(null);

    useEffect(() => {
        console.log("React App !");

        const fetchIpAddress = async () => {
            let tgData;
            if (BackendConfig.useMockData) {
                const tgResponse = await fetch(`${process.env.PUBLIC_URL}/mock/tgUser.json`);
                tgData = await tgResponse.json();
                console.log("    Telegram:", tgData);
            } else {
                if (window.Telegram?.WebApp) {
                    const tg = window.Telegram.WebApp;
                    tg.ready();
                    tgData = tg.initDataUnsafe?.user || null;
                }
            }

            let response;
            setTgUser(tgData);

            if (BackendConfig.useMockData) {
                response = await fetch(`${process.env.PUBLIC_URL}/mock/ip.json`);
            } else {
                response = await axios.get(BackendConfig.getLocationEndpoint);
            }

            const data = await response.json();

            setLocation(prevState => {
                const newState = {
                    ip: data.ip,
                    country: data.country || "",
                    city: data.city || "",
                    lat: data.lat || 0.0,
                    lon: data.lon || 0.0,
                };
                console.log("ip: " + newState.ip);
                return newState;
            });
        }
        fetchIpAddress();
    }, []);

    useEffect(() => {
        if (!tgUser || location.ip === "...") return; //    
        const sendUserData = async () => {
            try {
                let response;
                let data;

                if (BackendConfig.useMockData) {
                    response = await fetch(`${process.env.PUBLIC_URL}/mock/user.json`);
                    data = await response.json();
                    console.log("  :", data);
                } else {
                    response = await axios.post(`${BackendConfig.userEndpoint}`, {
                        telegramId: tgUser.id,
                        firstName: tgUser.first_name,
                        lastName: tgUser.last_name || "",
                        username: tgUser.username || "",
                        language: tgUser.language_code || "unknown",
                        ip: location.ip,
                        country: location.country,
                        city: location.city,
                        lat: location.lat,
                        lon: location.lon
                    });
                    data = response.data;
                    console.log("  :", data);
                }

                if (!data || !data.telegramId || typeof data.balance !== "number") {
                    throw new Error("   !");
                }

                setUserData(data);
            } catch (error) {
                console.error("    :", error);
            }
        };

        sendUserData();
    }, [tgUser, location]); //     `tgUser`  `location`

    useEffect(() => {
        if (tgUser?.language_code) {
            const supportedLanguages = Object.keys(i18n.options.resources);
            const userLang = supportedLanguages.includes(tgUser.language_code)
                ? tgUser.language_code
                : "en";
            i18n.changeLanguage(userLang);
        }
    }, [tgUser]);

    return (
        <>
            <Header userData={userData}/>
            <div className="app">
                <Container>
                    <Routes>
                        <Route path="/" element={<CurrentGames userData={userData} setUserData={setUserData}/>}/>
                        <Route path="/history" element={<History userData={userData}/>}/>
                        <Route path="/settings" element={<Settings userData={userData}/>}/>
                        <Route path="/promotion" element={<MakeModel userData={userData} setUserData={setUserData}/>}/>
                        <Route path="/admin" element={<AdminPanel userData={userData}/>}/>
                    </Routes>
                </Container>
            </div>
            <FootNav userData={userData}/>
        </>
    );
}
